*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
body {
  background-color: black;
  overflow-x: hidden;
  margin: 0%;
  width: 100vw;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo{
  height: 30px;
  position: absolute;
  top: 20px;
  left: 35px;
  text-decoration: none;
  
}

.navbar{
  position: fixed;
  width: 100vw;
  z-index:1;
  /* background-color: black; */
  color: rgb(0 199 255);
  /* height: px; */
  padding: 20px 15px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  
}


.navul{
  margin-left: 60px;
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.navli{
  margin: auto 10px;
  font-size: large;
  list-style: none;
  cursor: pointer;
}

.navroute{
  text-decoration: none;
  color: rgb(0 199 255);

}
.actroute{
  color: #e83e8c;
}

.social{
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 20px;
  margin-right: 30px;
}

.soclogos:hover{
    position: relative;
    bottom: 5px;
}

.banner{
  overflow: hidden;
  padding: 30px;
  
  background-color: black;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  
}
.logoban{

  margin-top: 7vh;
  margin-right: 130px;
  position: relative;
  right: 30px;
  transition: .5s;
  transform: rotate(20deg);
}
.coder3{
  position: relative;
  left: 320px;
  top: 10px;
  rotate: -20deg;
}


.tcont{
  margin-top: 10vh;
}

.hi{
  color: azure;
  position: relative;
  top: 40px;
  left: 60px;
}

.name{
  color: rgb(0 199 255);
  position: relative;
  font-size: 50px;
  top: 40px;
  left: 60px;
}

.desc{
  color: #e83e8c;
  font-size: 50px;
  position: relative;
  top: 40px;
  left: 60px;
}


.skills{
  /* background-color: rgb(4, 43, 54); */
  background-color: black;
  width: 100%;
  height: 100vh;
  padding: 30px;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  gap: 45px;
  
}


.skillth1{
  font-size: 50px;
  font-weight: bold;
  color: #e83e8c;
}
.iconboxl1{
  display: flex;
  padding: 15px;
  gap: 25px;
}
.icons{
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.iconboxl2{
  display: flex;
  padding: 15px;
  gap: 25px;
}

.icon{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: azure;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  width: 90px;
  height: 90px;
}

.svico{
  cursor: pointer;
  transition: transform .3s;
  transition-timing-function: ease-in-out;

}

.svico:hover{
  transform: scale(1.2);
  
}



.footer{
  height: 50vh;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  
  
}
.fcontent{
  display: flex;
  color: azure;
  padding: 30px;
 
  flex-direction: row;
  gap: 250px;
}

.projlistf{
  cursor: pointer;
  display: flex;
  margin-top: 10px;
  flex-direction: column;
  gap: 8px;
  font-size: 15px;
  margin-left: 9px;
  list-style: none;
}
.soclist{
  display: flex;
  margin-top: 7px;
  flex-direction: column;
  gap: 5px;
  font-size: 15px;
  /* margin-left: 5px; */
  list-style: none;
  
}

.socioli a{
  display: flex;
  flex-direction: row;
  gap: 3px;
  align-items: center;
  height: 32px;
  color: azure;
  text-decoration: none;
}

.line{
  width: 90vw;
  
 border-top: azure 2px solid;
 opacity: 50%;
}

.gitsource{
  cursor: pointer;
  text-decoration: none;
  margin-top: 70px;
 display: flex;
 gap: 5px;
 transition: .5s;
 transition-timing-function: ease-in-out;
 align-items: center;
 border: 2px rgb(0 199 255) solid;
 border-radius: 10px;
 color: rgb(0 199 255);
 padding: 5px;
 height: 50px;
 
}
.gitsource:hover{
  transform: scale(1.1);
}

.love{
  color: azure;
  margin-top: 45px;
}

.trails{
  position: absolute;
}
#trail1{
  right: 350px;
  top: 50px;
  rotate: -45deg;
}
#trail2{
  right: -470px;
  transform: scaleX(-1);
  top: 50px;
  rotate: 45deg;
}
#trail3{
  right: 350px;
  top: 250px;
  rotate: -80deg;
}
#trail4{
  right: -470px;
